*{
    padding: 0;
    margin: 0;
}
#header{
    width: 100%;
    height: 3cap;
    font-size: 2cap;
    text-align: center;
    justify-content: center;
    background-color: rgb(127, 127, 222);
}
#home > a{
    text-decoration: none;
    padding: 15px;

}
#contact{
    background-color: rgb(199, 157, 120);
    min-height: 100vb;
    width: 70%;
    margin: auto;
    text-align: center;
}
#contact >h3{
    text-align: center;
    padding: 10px;
    font-size: 30px;
}
#goal{
    width: 90%;
    height: 4cap;
    margin: auto;
    display: flex;
    border-radius: 10px;
    margin-bottom: 1cap;
    font-size: medium;
    font-family: sans-serif;
    padding: 5px;
    
}
#descpition{
    width:90%;
    height: 8cap;
    margin: auto;
    border-radius: 10px;
    display: flex;
    font-size: medium;
    font-family: sans-serif;
    padding: 10px;
}
#submitbtn{
    background-color: rgb(106, 106, 213);
    width: 93%;
    height: 3cap;
    margin: auto;
    border-radius: 20px;
    padding: 10px;
    text-align: center;
    font-size: medium;
    margin-top: 1cap;
}
#task{
    display: flex;
    width: 90%;
    min-height: 5cap;
    background-color: azure;
    display: flex;
    margin: auto;
    padding: 5px;
    border-radius: 10px;
    margin-top: 1cap;
}
#task >div{
    width: 100%;
    text-align: left;   
    margin: auto;
    padding: auto;
}

#task > button{
    width: 30px;
    height: 30px;
    border-radius: 100%;
    background-color: black;
    font-size: large;
    color: white;
    margin: auto;
    text-align: centers;

}
#task> button:hover{
    background-color: red;
    color: black;
}